import { makeExecutableSchema } from "graphql-tools";
import { resolvers } from "./resolvers";

const typeDefs = `
type Query {
    hello: String
    greet(name: String):String
    par(numero: Int!): Boolean
    saludo(nombre: String):String
    varios(num: Int!):Int    
    ingredientes:[Ingredientes]
    ingrediente(id: Int, nombre: String):Ingredientes
    pizzas(id: Int,nombre: String):[Pizzas]
  }
  type Mutation{
    createIngrediente(input: inputIngrediente): Ingredientes
  }
  input inputIngrediente{
    nombre: String!
  }
  type Ingredientes{
      id:ID
      nombre: String
  }
  type Pizzas{
    id:ID
    nombre: String
    precio: Float
    ingredientes: [Ingredientes]
}
`;

export default makeExecutableSchema({
  typeDefs: typeDefs,
  resolvers: resolvers,
});
